@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700);
footer{background-color:#000;position:fixed;bottom:0;width:100%;min-height:50px;display:table}footer a{color:skyblue;font-size:1.5rem;text-decoration:none;font-weight:bolder;margin:auto;text-align:center;display:table-cell;vertical-align:middle}
html,body{margin:0;padding:0;box-sizing:border-box}header{font-family:"Quicksand",sans-serif;font-size:62.5%;position:fixed;top:0}.nav{width:100%;height:35px;position:fixed;line-height:30px;text-align:center;z-index:2;background-color:#000;min-height:30px;padding-top:20px;padding-bottom:20px;transition:all .4s ease}.nav div .logoName{float:left;width:auto;height:auto;padding-left:3rem}.nav div.logoName a{text-decoration:none;color:skyblue;font-size:2rem}.nav div.logoName a:hover{color:#000}.nav div.main_list{height:65px;float:right}.nav div.main_list ul{width:100%;height:65px;display:flex;list-style:none;margin:0;padding:0}.nav div.main_list ul li{width:auto;height:65px;padding:0;padding-right:3rem}.nav div.main_list ul li a{text-decoration:none;color:skyblue;line-height:65px;font-size:2rem}.nav div.main_list ul li a:hover{color:#000}
.inputDiv{text-align:center}.searchInput{border:1px solid #000;outline:1px solid #000;border-radius:5px;margin:5.5rem auto auto 2rem;padding:.5rem;text-align:center;align-items:center;width:37%}
.users{width:95%;height:95%;padding:1rem;margin:1rem;border-radius:5px;text-align:center;margin-bottom:100px}.singleUser{width:20%;display:inline-block;border:1px solid gray;border-radius:5px;margin:.5rem;box-shadow:0 .2rem .5rem rgba(0,0,0,.35);background-color:#dcdcdc}.singleUserAvatar{width:50%;margin:1rem}.noResultsFound{margin:9rem auto;text-align:center;font-size:3rem;font-weight:bolder}
.reposDiv{padding-top:100px;margin-bottom:100px;text-align:center}.singleUserDiv{border:1px solid #000;border-radius:5px;margin:1rem;padding:.5rem;width:45%;display:inline-block;vertical-align:top;height:14rem;background-color:#dcdcdc;box-shadow:0 .5rem 1rem rgba(0,0,0,.35)}.center{display:flex;text-align:center;align-items:center;justify-content:center}.openRepositoryButton{margin:.5rem;padding:.3rem;background-color:skyblue;border-radius:5px}.repoDescription{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:3;line-clamp:3;-webkit-box-orient:vertical}.userName{display:block}
.singleRepoDiv{display:block;margin:3rem;border-radius:5px;font-size:1.5rem;text-align:center}.repositoryName{margin-top:100px}.boldSpan{font-weight:bolder}
body{min-height:100vh;display:flex;flex-direction:column;background-color:#f5f5f5}

